body {
  font-family: 'Roboto', sans-serif;
  background: linear-gradient(to left, #43C6AC, #191654);
}

#root {
  margin: 100px auto;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
  width: 360px;
  background-color: #f7f7f7;
}
h1 {
    background-color: #2980b9;
    color: white;
    margin: 0;
    padding: 10px 20px;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: normal;
}

.fa-plus,
.fa-search {
    float: right;
    background: none;
    border: none;
    color: white;
    font-size: 25px;
    outline: none;
    padding-left: 15px;
}


#container {
    margin: 100px auto;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
    width: 360px;
    background-color: #f7f7f7;
}

input {
    font-size: 18px;
    background-color: #f7f7f7;
    width: 100%;
    padding: 13px 13px 13px 20px;
    box-sizing: border-box;
    color: #2980b9;
    border: 3px solid rgba(0, 0, 0, 0);
}
ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

li {
    background-color: #fff;
    height: 40px;
    line-height: 40px;
    color: #666;
}

.fa-trash {
    background-color: #e74c3c;
    height: 40px;
    margin-right: 20px;
    text-align: center;
    color: white;
    width: 0;
    display: inline-block;
    transition: 0.2s linear;
    opacity: 0;
    float: left;
}

li:nth-child(2n) {
    background-color: #f7f7f7;
}

input:focus {
    background-color: white;
    border: 3px solid #2980b9;
}

li:hover .fa-trash {
    width: 40px;
    opacity: 1.0;
    border: none;
}

li.completed {
    color: gray;
    text-decoration: line-through;
    background: rgba(0, 0, 0, 0.2)
}
